import Link from 'next/link'
import React, { useState, useEffect } from 'react'
import UserDetailsModal from './UserDetailsModal'

const MainScreenWrapper = ({
    children,
    screenHeader = 'Header',
}) => {
    const [showModal, setShowModal] = useState(false);

    useEffect(() => {
        // Check if this is the first visit
        const hasVisited = localStorage.getItem('hasVisited');
        
        if (!hasVisited) {
            setShowModal(true);
        }
    }, []);

    const handleModalSubmit = (details) => {
        // Mark as visited and save user details
        localStorage.setItem('hasVisited', 'true');
        localStorage.setItem('userDetails', JSON.stringify(details));
        setShowModal(false);
    };

    return (
        <>
            <UserDetailsModal 
                isOpen={showModal} 
                onSubmit={handleModalSubmit} 
            />
            
            <div className={`justify-start relative w-full flex h-full items-start flex-col min-h-screen gap-1 text-black ${showModal ? 'blur-sm pointer-events-none' : ''}`} >
                {/* {headerItem} */}
                <div className="w-full h-full pt-1">
                    {children}
                </div>
            </div>
        </>
    )
}
export default MainScreenWrapper
